import React, { useState, useEffect } from "react";
import axios from "axios";
import { useLocation } from "react-router-dom";
import NavbarHeader from "./navbar";

const ProductItemView = () => {
  const location = useLocation();
  const title = location.state?.title;
  const [currProduct, setCurrProduct] = useState({});
  const [currentSlide, setCurrentSlide] = useState(0);

  const handlePrev = () => {
    setCurrentSlide((prevSlide) => (prevSlide - 1 + 15) % 15);
    console.log(currentSlide);
  };

  const handleNext = () => {
    setCurrentSlide((prevSlide) => (prevSlide + 1 + 15) % 15);
    console.log(currentSlide);
  };

  const handleSlide = (index) => {
    setCurrentSlide(index);
  };

  const handleFetchProduct = (data) => {
    // const url = "https://dummyjson.com/products/";
    const url = "http://127.0.0.1:8000/api/v1/products/";

    axios({
      data: data,
      method: "POST",
      url,
      headers: {
        "Content-Type": "application/json",
      },
    })
      .then((res) => {
        const { status, data } = res;
        setCurrProduct(data?.data);
        console.log("data", data);
      })
      .catch((error) => {
        console.log("Error", error);
      });
  };

  const handleEditProduct = (data) => {
    // const url = "https://dummyjson.com/products/";
    const url = "http://127.0.0.1:8000/api/v1/edit_product/";

    axios({
      data: data,
      method: "POST",
      url,
      headers: {
        "Content-Type": "application/json",
      },
    })
      .then((res) => {
        const { status, data } = res;
        setCurrProduct(data?.data);
        console.log("data", data);
      })
      .catch((error) => {
        console.log("Error", error);
      });
  };

  const handleFavouriteClick = () => {
    const data = {
      title: currProduct.title,
      is_favourite: !currProduct.is_favourite,
    };
    handleEditProduct(data);
  };

  const handleAddToCartClick = () => {
    const data = {
      title: currProduct.title,
      is_item_in_cart: !currProduct.is_item_in_cart,
      quantity: currProduct.quantity + 1,
    };

    handleEditProduct(data);
  };

  useEffect(() => {
    handleFetchProduct({ title: title });
  }, []);

  return (
    <>
      <h1>Hello</h1>
    </>
  );
};

export default ProductItemView;
